<template>
    <div class="proAdd">
        <h2>商品新增</h2>
        <el-form :model="form">
            <el-form-item label="商品名">
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="图片">
                <el-upload
                action="http://api.cat-shop.penkuoer.com/api/v1/common/file_upload"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                >
                    <img v-if="imageUrl" :src="form.coverImg | coverImg" class="avatar" alt="">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <el-form-item label="价格">
                <el-input v-model="form.price"></el-input>
            </el-form-item>
            <el-form-item label="描述">
                <el-input v-model="form.descriptions"></el-input>
            </el-form-item>
            <el-form-item label="库存">
                <el-input v-model="form.quantity"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" plain @click="onSubmit()">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import { addProlist } from '../../api/product'
    export default {
        name:"productAdd",
        data() {
            return {
                dialogFormVisible:false,
                imageUrl:'',
                form:{
                    name:'',
                    coverImg:'',
                    price:'',
                    descriptions:'',
                    quantity:'',
                }
            }
        },
        methods: {
            handleAvatarSuccess(res, file) {
                this.form.coverImg = res.info
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            onSubmit(){
                addProlist(this.form).then(res=>{
                    if(res.status == 200){
                        this.$router.push('/product')
                    }else{
                        alert('添加失败')
                    }

                })
            }
        },
        filters:{
            coverImg(img){
                let str = 'http://api.cat-shop.penkuoer.com' + img
                return img.startsWith('http') ? img : str
            },
            sale(onSale){
                if (onSale==false) {
                    return "是"
                }else{
                    return "否"
                }
            }
        },
    }
</script>

<style scoped>
.proAdd{
    width: 460px;
    height: 500px;
    padding: 20px 100px;
    border-radius: 20px;
    background: rgba(0, 0, 0, .1);
    position: absolute;
    top: 40px;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}
 .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 90px;
    height: 90px;
    line-height: 90px;
  }
    .avatar {
    max-width: 90px;
    max-height: 90px;
    display: block;
  }
  .el-form-item{
      margin-bottom: 5px
  }
</style>