import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/login/Index.vue'
import User from '../views/users/Index.vue'
import Product from '../views/product/Index.vue'
import ProAdd from '../views/product/New.vue'
import Order from '../views/order/Index.vue'
import UserAdd from '../views/users/UserAdd.vue'


Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    redirect:'product',
    name: 'Home',
    component: Home,
    meta: {
      title:"首页"
    },
    children:[
      {
        path:'user',
        name:'User',
        component: User,
        meta: {
          title:"用户中心"
        }
      },
      {
        path:'userAdd',
        name:'UserAdd',
        component: UserAdd
      },
      {
        path:'product',
        name:'Product',
        component: Product,
        meta: {
          title:"商品信息"
        }
      },
      {
        path:'proAdd',
        name:'ProAdd',
        component: ProAdd,
        meta: {
          title:"商品新增"
        }
      },
      {
        path:'order',
        name:'Order',
        component: Order,
        meta: {
          title:"订单列表"
        }
      },
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      title:"登录"
    }
  },
]



const router = new VueRouter({
  routes
})

export default router