<template>
    <div class="users">
        <el-input class="ipt" v-model="ipt" placeholder="请输入需要查询的商品名字" clearable></el-input>
        <el-button type="success" round @click="search">搜索</el-button>
        <el-table class="tab" :data="list" height="95%" border style="width: 100%">
            <el-table-column type="index" label="序号"></el-table-column>
            <el-table-column label="商品名字">
                <template slot-scope="scope">
                    <div class="ellipsis">{{scope.row.name}}</div>
                </template>
            </el-table-column>
            <el-table-column label="商品id">
                <template slot-scope="scope">
                    <div class="ellipsis">{{scope.row._id}}</div>
                </template>
            </el-table-column>
            <el-table-column label="图片">
                <template slot-scope="scope">
                    <img :src="scope.row.coverImg | coverImg" alt="">
                </template>
            </el-table-column>
            <el-table-column prop="price" label="价格"></el-table-column>
            <el-table-column prop="quantity" label="库存"></el-table-column>
            <el-table-column label="描述">
                <template slot-scope="scope">
                    <div class="ellipsis">{{scope.row.descriptions}}</div>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button  size="small"  icon="el-icon-edit" type="primary" plain @click="edit(scope.row._id)" >修改</el-button>
                    <el-button  size="small" icon="el-icon-delete" type="danger" plain @click="delOne(scope.row._id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination @current-change="pageChange" class="ye" background layout="prev, pager, next" :page-count="pages"></el-pagination>

        <el-dialog title="商品信息修改" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                    <el-form-item label="商品名">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="图片">
                        <el-upload
                        action="http://api.cat-shop.penkuoer.com/api/v1/common/file_upload"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        >
                            <img :src="form.coverImg | coverImg" alt="">
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="价格">
                        <el-input v-model="form.price"></el-input>
                    </el-form-item>
                    <el-form-item label="描述">
                        <el-input v-model="form.descriptions"></el-input>
                    </el-form-item>
                    <el-form-item label="库存">
                        <el-input v-model="form.quantity"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" plain @click="onSubmit(form._id)">立即修改</el-button>
                        <el-button type="info" plain @click="cancel">取消</el-button>
                    </el-form-item>
                </el-form>
        </el-dialog>
    </div>
</template>

<script>
import { getProlist, setProlist, delProlist, getProDetail } from '../../api/product.js'
    export default {
        name:'User',
         data() {
            return {
                imageUrl: '',
                list: [],
                ipt:'',
                pages:1,
                gridData: [],
                dialogFormVisible: false,
                form:{
                    name:'',
                    coverImg:'',
                    price:'',
                    descriptions:'',
                    quantity:'',
                    _id:''
                }
            }
        },
        async mounted() {
            this.loadData()
        },
        filters:{
            coverImg(img){
                let str = 'http://api.cat-shop.penkuoer.com' + img
                return img.startsWith('http') ? img : str
            },
            sale(onSale){
                if (onSale==false) {
                    return "是"
                }else{
                    return "否"
                }
            }
        },
        methods: {
            async pageChange(page){
                this.loadData(page)
            },
            async loadData(page=1){
                // console.log(1111111111)
                const res = await getProlist(20,page,this.ipt)
                this.list = res.data.products
                this.pages = res.data.pages
            },
            async edit(id){
                this.dialogFormVisible = true
                let result = await getProDetail(id)
                this.form._id = result.data._id
                this.form.name = result.data.name
                this.form.coverImg = result.data.coverImg
                this.form.price = result.data.price
                this.form.descriptions = result.data.descriptions
                this.form.quantity = result.data.quantity

            },
            delOne(id){
                this.$confirm('此操作将永久删除该商品, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                }).then(() => {
                    delProlist(id).then(res=>{
                        this.$router.go(0)
                    })
                    this.$message({
                    type: 'success',
                    message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '已取消删除'
                    });
                });
            },
            async onSubmit(id, params){
                this.dialogFormVisible = false
                const res = await setProlist(id,this.form)
                alert("修改成功")
                this.$router.go(0)
            },
            cancel(){
                this.dialogFormVisible = false
            },
            handleAvatarSuccess(res, file) {
                this.form.coverImg = res.info
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            search(page){
                this.loadData(page,20,this.ipt)
            }
        }
    }
</script>

<style scoped>
.users{
    width: 100%;
    height: 88%;
}
.ye{
    text-align: left
}
img{
    max-width: 100px;
    max-height: 100px;
}
.ellipsis{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
}
.tab{
    margin-top: 15px
}
.avatar {
max-width: 100px;
max-height: 100px;
display: block;
}
.ipt{
    width: 40%;
    margin-right: 10px
}
</style>