<template>
    <div class="userAdd">
        <h1>用户新增</h1>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="账号" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="psw">
                <el-input v-model="ruleForm.psw"></el-input>
            </el-form-item>
            <el-form-item label="昵称">
                <el-input v-model="ruleForm.nickName"></el-input>
            </el-form-item>
            <el-button type="primary" round @click="saveInfo">保存</el-button>
        </el-form>
    </div>
</template>

<script>
import { addUser } from '../../api/product.js'
    export default {
        name:"UserAdd",
        data() {
      return {
        ruleForm: {
          name: '',
          psw:'',
          nickName:''
        },
        rules: {
          name: [
            { required: true, message: '请输入账户名', trigger: 'blur' },
            { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          psw: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      saveInfo(){
        addUser({
            'userName':this.ruleForm.name,
            'password':this.ruleForm.psw,
            'nickName':this.ruleForm.nickName,
            'avatar':''
        })
        .then(res=>{
          console.log(res)
            if(res.data.message == "userName exist!"){
                alert("用户名重复")
            }else{
                alert("添加用户成功")
                this.$router.push('/user')
            }
        })
      }
    },

    }
</script>

<style scoped>
.userAdd{
    width: 600px;
    height: 290px;
    background: rgba(0, 0, 0, .1);
    border-radius: 50px;
    padding: 30px 70px 50px 20px;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto
}
h1{
    font-weight: normal;
    font-size: 20px;
    margin-bottom: 30px
}
</style>