import * as API from './index.js'
import axios from 'axios'



//获取登录接口
export const login = (params)=>{
    return API.POST('/api/v1/auth/manager_login',params)
}

//获取用户列表
export const getUserList = (page,per,userName)=>{
    return API.GET('/api/v1/admin/users',{page,per,userName})
}

//修改用户信息
export const setUserlist = (id,params)=>{
    return API.PUT('/api/v1/admin/users/'+id,params)
}

//新增用户信息
export const addUser = (params)=>{
    return API.POST('/api/v1/admin/users',params)
}

//删除用户信息
export const delUserlist = (id)=>{
    return API.DELETE('/api/v1/admin/users/'+id)
}

//用户详细信息
export const getUserDetail = (id)=>{
    return API.GET('/api/v1/admin/users/'+id)
}


//获取商品列表接口
export const getProlist = (per,page,name)=>{
    // console.log(localStorage.getItem('token'))
    return API.GET('/api/v1/admin/products',{per,page,name})
}

//修改商品信息
export const setProlist = (id,params)=>{
    return API.PUT('/api/v1/admin/products/'+id,params)
}

//新增商品信息
export const addProlist = (params)=>{
    return API.POST('/api/v1/admin/products',params)
}

//删除商品信息
export const delProlist = (id)=>{
    return API.DELETE('/api/v1/admin/products/'+id)
}

//获取商品详情
export const getProDetail = (id)=>{
    return API.GET('/api/v1/admin/products/'+id,{})
}

//订单

//获取订单列表
export const getOrderlist = (page,per,no)=>{
    return API.GET('/api/v1/admin/orders',{page,per,no})
}

//修改订单信息
export const setOrderlist = (id,params)=>{
    return API.PUT('/api/v1/admin/orders/'+id,params)
}

//删除订单信息
export const delOrderlist = (id)=>{
    return API.DELETE('/api/v1/admin/orders/'+id)
}

//订单详情
export const getOrderDetail = (id)=>{
    return API.GET('/api/v1/admin/orders/'+id,{})
}
