<template>
    <div class="log">
        <h2>login</h2>
        <div>
            <span>账号：</span>
            <el-input class="ipt" placeholder="请输入内容" v-model="username" clearable></el-input>
        </div>
        <div>
            <span>密码：</span>
            <el-input class="ipt" placeholder="请输入密码" v-model="password" show-password></el-input>
        </div>
        <el-button type="primary" @click="log()">登录</el-button>

    </div>
</template>

<script>
import axios from 'axios'
import { login } from '../../api/product.js'
    export default {
        name:"Login",
        data() {
            return {
                username: '',
                password: ''
            }
        },
        methods: {
            async log(){
                const res = await login({userName:this.username,password:this.password})
                if(res.data.code == "success"){
                    localStorage.setItem("token",res.data.token);
                    axios.defaults.headers.common['authorization'] = "Bearer "+res.data.token;
                    this.$router.push('/')
                }else{
                    alert("账号密码错误")
                }

            }
        },
    }
</script>

<style scoped>
h2{
    margin-bottom: 30px;
}
.log{
    width: 500px;
    height: 300px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    padding: 30px 30px;
    position: absolute;
    top: 230px;
    right: 150px;

}
.ipt{
    width: 60%;
    margin-bottom: 20px
}
</style>