<template>
    <div class="users">
        <el-input class="ipt" v-model="ipt" placeholder="请输入需要查询的账号" clearable></el-input>
        <el-button type="success" round @click="search">搜索</el-button>
        <el-table class="tab" :data="list" height="95%" border style="width: 100%">
            <el-table-column type="index" label="序号"></el-table-column>
            <el-table-column prop="_id" label="id"></el-table-column>
            <el-table-column prop="userName" label="账号"></el-table-column>
            <el-table-column prop="password" label="密码"></el-table-column>
            <el-table-column prop="nickName" label="昵称"></el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button  size="small" icon="el-icon-edit" type="primary" plain @click="edit(scope.row._id)" >修改</el-button>
                    <el-button  size="small" icon="el-icon-delete" type="danger" plain @click="delOne(scope.row._id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination @current-change="pageChange" class="ye" background layout="prev, pager, next" :page-count="pages"></el-pagination>

        <el-dialog title="用户信息修改" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                    <el-form-item label="id">
                        <el-input v-model="form._id" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="账号">
                        <el-input v-model="form.userName"></el-input>
                    </el-form-item>
                    <el-form-item label="密码">
                        <el-input v-model="form.password"></el-input>
                    </el-form-item>
                    <el-form-item label="昵称">
                        <el-input v-model="form.nickName"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" plain @click="onSubmit(form._id)">立即修改</el-button>
                        <el-button type="info" plain @click="cancel">取消</el-button>
                    </el-form-item>
                </el-form>
        </el-dialog>
    </div>
</template>

<script>
import { getUserList, setUserlist, delUserlist, getUserDetail } from '../../api/product.js'
    export default {
        name:'User',
         data() {
            return {
                ipt:'',
                list: [],
                pages:1,
                gridData: [],
                dialogFormVisible: false,
                form:{
                    userName:'',
                    password:'',
                    _id:'',
                    nickName:''
                }
            }
        },
        async created() {
            this.loadData()
        },
        methods: {
            async pageChange(page){
                this.loadData(page)
            },
            async loadData(page=1){
                const res = await getUserList(page,20,this.ipt)
                res.data.users.forEach(item=>{
                    if(item.nickName){
                        item.nickName = item.nickName
                    }else{
                        item.nickName = "artificial"
                    }
                })
                this.list = res.data.users
                this.pages = res.data.pages
            },
            async edit(id){
                this.dialogFormVisible = true
                let result = await getUserDetail(id)
                this.form.userName = result.data.userName
                this.form._id = result.data._id
                this.form.password = result.data.password
                this.form.nickName = result.data.nickName
            },
            delOne(id){
                this.$confirm('此操作将永久删除该账号, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                }).then(() => {
                    delUserlist(id).then(res=>{
                        this.$router.go(0)
                    })
                    this.$message({
                    type: 'success',
                    message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '已取消删除'
                    });
                });
            },
            async onSubmit(id,params){
                this.dialogFormVisible = false
                const res = await setUserlist(id,this.form)
                alert("修改成功")
                this.$router.go(0)
            },
            cancel(){
                this.dialogFormVisible = false
            },
            search(page){
                this.loadData(page,20,this.ipt)
            }
        },
    }
</script>

<style scoped>
.users{
    width: 100%;
    height: 88%;
}
.tab{
    margin-top: 15px;
}
.ye{
    text-align: left
}
.ipt{
    width: 40%;
    margin-right: 10px
}
</style>