<template>
    <div class="users">
        <el-input class="ipt" v-model="ipt" placeholder="请输入需要查询的订单单号" clearable></el-input>
        <el-button type="success" round @click="search">搜索</el-button>
        <el-table class="tab" :data="list" height="95%" border style="width: 100%">
            <el-table-column type="index" label="序号"></el-table-column>
            <el-table-column label="订单id">
                <template slot-scope="scope">
                    <div class="ellipsis">{{scope.row._id}}</div>
                </template>
            </el-table-column>
            <el-table-column label="订单单号">
                <template slot-scope="scope">
                    <div class="ellipsis">{{scope.row.no}}</div>
                </template>
            </el-table-column>
            <el-table-column label="所属用户">
                <template slot-scope="scope">
                    <div class="ellipsis">{{scope.row.receiver}}</div>
                </template>
            </el-table-column>
            <el-table-column prop="price" label="价格"></el-table-column>
            <el-table-column label="地址">
                <template slot-scope="scope">
                    <div class="ellipsis">{{scope.row.address}}</div>
                </template>
            </el-table-column>
            <el-table-column label="是否支付">
                <template slot-scope="scope">
                    <div>
                        {{scope.row.isPayed | sale}}
                    </div>
                </template>
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button  size="small"  icon="el-icon-edit" type="primary" plain @click="edit(scope.row._id)" >修改</el-button>
                    <el-button  size="small"  icon="el-icon-delete" type="danger" plain @click="delOne(scope.row._id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination @current-change="pageChange" class="ye" background layout="prev, pager, next" :page-count="pages"></el-pagination>

        <el-dialog title="订单修改" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                    <el-form-item label="支付状态">
                        <el-input v-model="form.isPayed"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" plain @click="onSubmit(form._id)">立即修改</el-button>
                        <el-button type="info" plain @click="cancel">取消</el-button>
                    </el-form-item>
                </el-form>
        </el-dialog>
    </div>
</template>

<script>
import { getOrderlist, setOrderlist, delOrderlist,getOrderDetail  } from '../../api/product.js'
    export default {
        name:'User',
         data() {
            return {
                ipt:'',
                imageUrl: '',
                list: [],
                pages:1,
                gridData: [],
                dialogFormVisible: false,
                form:{
                    no:'',
                    address:'',
                    price:'',
                    descriptions:'',
                    receiver:'',
                    isPayed: ''
                }
            }
        },
        async created() {
            this.loadData()
        },
        filters:{
            sale(onSale){
                if (onSale == false) {
                    return "否"
                }else{
                    return "是"
                }
            }
        },
        methods: {
            async pageChange(page){
                this.loadData(page)
            },
            async loadData(page=1){
                const res = await getOrderlist(page,20,this.ipt)
                // console.log(res)
                this.list = res.data.orders
                this.pages = res.data.pages
            },
            async edit(id){
                this.dialogFormVisible = true
                let result = await getOrderDetail(id)
                Boolean(result.data.isPayed)
                this.form = result.data
            },
            delOne(id){
                this.$confirm('此操作将永久删除该订单, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                }).then(() => {
                    delOrderlist(id).then(res=>{
                        // console.log(res)
                        this.$router.go(0)
                    })
                    this.$message({
                    type: 'success',
                    message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                    type: 'info',
                    message: '已取消删除'
                    });
                });
            },
            async onSubmit(id, params){
                this.dialogFormVisible = false
                const res = await setOrderlist(id,this.form)
                alert("修改成功")
                this.$router.go(0)
            },
            cancel(){
                this.dialogFormVisible = false
            },
            handleAvatarSuccess(res, file) {
                this.form.coverImg = res.info
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            search(page){
                this.loadData(page,20,this.ipt)
            }
        }
    }
</script>

<style scoped>
.users{
    width: 100%;
    height: 88%;
}
.ye{
    text-align: left
}
.tab{
    margin-top: 1%
}
img{
    max-width: 100px;
    max-height: 100px;
}
.ellipsis{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
}
.el-table__row{
    min-height: 200px;
}
.ipt{
    width: 40%;
    margin-right: 10px
}
</style>