<template>
    <div class="hom">
        <el-container style="border: 1px solid #eee">
            <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
                <el-menu :default-openeds="['1', '2' ,'3']">
                    <el-submenu index="1">
                        <template slot="title"><i class="el-icon-goods"></i>商品管理</template>
                        <el-menu-item-group>
                        <el-menu-item index="2-1" @click="proList">商品列表</el-menu-item>
                        <el-menu-item index="2-2" @click="proAdd">商品新增</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-submenu index="2">
                        <template slot="title"><i class="el-icon-user"></i>用户管理</template>
                        <el-menu-item-group>
                        <el-menu-item index="1-1" @click="userList">用户列表</el-menu-item>
                        <el-menu-item index="1-2" @click="userAdd">用户新增</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                    <el-submenu index="3">
                        <template slot="title"><i class="el-icon-setting"></i>订单管理</template>
                        <el-menu-item-group>
                        <el-menu-item index="3-1" @click="order">订单列表</el-menu-item>
                        </el-menu-item-group>
                    </el-submenu>
                </el-menu>
            </el-aside>

            <el-container>
                <el-header style="text-align: right; font-size: 12px">
                    <el-dropdown>
                        <i class="el-icon-setting" style="margin-right: 15px"></i>
                        <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item>
                            <el-button type="text" @click="logOut">退出登录</el-button>
                        </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                    <span>超级管理员</span>
                </el-header>
                <el-main>
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
    export default {
        data() {
            return {

            }
        },
        methods: {
            logOut(){
                this.$confirm('是否退出登录?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    center: true
                    }).then(() => {
                        this.$message({
                            type: 'success',
                            message: '您已经退出登陆了呢**欢迎下次再来'
                        });
                        localStorage.removeItem('token')
                        this.$router.push('/login')
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '嘻嘻，您还可以继续访问后台管理系统呢！！！！'
                        });
                });
            },
            userList(){
                this.$router.push('/user')
            },
            userAdd(){
                this.$router.push('/userAdd')
            },
            proList(){
                this.$router.push('/product')
            },
            proAdd(){
                this.$router.push('/proAdd')
            },
            order(){
                this.$router.push('/order')
            },
        },
        created() {
            if(localStorage.getItem('token')){

            }else{
                this.$router.push('/login')
            }
        },
    }
</script>

<style scoped>
.hom{
    width: 100%;
    height: 85%;
    padding-top: 3%
}
.el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  .el-aside {
    color: #333;
  }
.el-container{
    height: 100%;
}
</style>